$positive: white;
$negative: black;
$theme-1-primary: rgb(93, 202, 202);
$theme-2-primary: rgb(207, 72, 207);
$theme-3-primary: rgb(192, 192, 59);
$theme-4-primary: gainsboro;
$line-color: rgb(197, 77, 77);

// light mode
$ipositive: black;
$inegative: white;
$itheme-1-primary: rgb(162, 53, 53);
$itheme-2-primary: rgb(48, 183, 48);
$itheme-3-primary: rgb(63, 63, 196);
$itheme-4-primary: rgb(169, 169, 169);
$iline-color: rgb(58, 178, 178);

.maingames {
    position: absolute;
    width: 100%;
    height: auto;
    display: flex;
    flex-direction: column;
    //top: 400px;
}

.maingames .title {
    color: var(--theme-1-primary);
    font-family: "VCR";
    font-size: 100px;
    text-align: end;
    //@at-root: desaturate(darken($theme-1-primary, 10%), 20%) 5px 5px;
    transition: 0.2s ease-in-out;
    margin-top: 200px;
}

//.light .maingames .title {
//    text-shadow: desaturate(darken($itheme-1-primary, 10%), 20%) 5px 5px;
//}

.maingames .section {
    color: var(--theme-1-primary);
    font-size: 100px;
    height: auto;
    text-align: center;
    transition: 0.2s ease-in-out;
    font-family: "VCR";
    font-size: 50px;
    //margin-bottom: 500px;
}

.item {
    margin: 0px;
    padding: 0px;
}

//.light .maingames .section {
//    background: desaturate(darken($itheme-1-primary, 10%), 20%);
//}

#launchScale {
    transform: scale(1);
    transition: 0.5s ease-out;
}

#launchScale.closed {
    transform: scale(0);
    transition: 0.5s ease-in;
}

#launchThing {
    margin-left: auto;
    margin-right: auto;
    position: relative;
    width: 600px;
    height: 400px;
    background-color: rgb(155, 173, 183);
    border: solid;
    border-width: 36px;
    border-image: url("../graphics/window.png") 36;
    pointer-events: all;
    animation: wiggle-float-window 5s ease-in-out infinite;
}

#launchTitle {
    color: white;
    font-family: "cappx";
    font-size: 64px;
    position: absolute;
    left: -24px;
    top: -54px;
}

#launchMinimize {
    cursor: pointer;
    position: absolute;
    background-image: url("../graphics/minimize.png");
    height: 32px;
    width: 32px;
    top: -32px;
    right: 20px;
}

#launchMinimize:hover {
    background-image: url("../graphics/minimize_hover.png");
}

#launchClose {
    cursor: pointer;
    position: absolute;
    background-image: url("../graphics/close.png");
    height: 32px;
    width: 32px;
    top: -32px;
    right: -20px;
}

#launchClose:hover {
    background-image: url("../graphics/close_hover.png");
}

#okbutton {
    cursor: pointer;
    background-image: url("../graphics/ok.png");
    background-size: 200px 100px;
    image-rendering: pixelated;
    height: 100px;
    width: 200px;
    position: absolute;
    left: 25%;
    top: 60%;
    transform: translateX(-50%) translateY(0);
    transition: 0.3s ease-in-out;
}

#okbuttonfloor {
    background-image: url("../graphics/ok_floor.png");
    background-size: 200px 100px;
    image-rendering: pixelated;
    height: 100px;
    width: 200px;
    position: absolute;
    left: 25%;
    top: calc(60% + 16px);
    transform: translateX(-50%) translateY(0);
    z-index: -1;
}

#okbutton:hover {
    transform: translateX(-50%) translateY(16px);
}

#nobutton {
    cursor: pointer;
    background-image: url("../graphics/no.png");
    background-size: 200px 100px;
    image-rendering: pixelated;
    height: 100px;
    width: 200px;
    position: absolute;
    left: 75%;
    top: 60%;
    transform: translateX(-50%) translateY(0);
    transition: 0.3s ease-in-out;
}

#nobuttonfloor {
    background-image: url("../graphics/no_floor.png");
    background-size: 200px 100px;
    image-rendering: pixelated;
    height: 100px;
    width: 200px;
    position: absolute;
    left: 75%;
    top: calc(60% + 16px);
    transform: translateX(-50%) translateY(0);
    z-index: -1;
}

#nobutton:hover {
    transform: translateX(-50%) translateY(16px);
}

#launchLabel {
    position: absolute;
    width: 600px;
    height: 150px;
    background-size: 600px 150px;
    top: 20%;
    image-rendering: pixelated;
    background-image: url("../graphics/launch.png");
}

#windowThing {
    position: relative;
    height: 100%;
    width: 100%;
}

#exe {
    cursor: pointer;
    pointer-events: all;
    position: absolute;
    //display: none;
    background-image: url("../graphics/exe.png");
    image-rendering: pixelated;
    height: 64px;
    width: 64px;
    background-size: 64px 64px;
    left: calc(50% - 32px);
    top: calc(50% - 32px);
    margin-top: auto;
    animation: wiggle-float-window 5s ease-in-out infinite;
    transition: 0.3s ease-in-out;
}

#exe div {
    color: var(--positive);
    position: absolute;
    font-family: "cappx";
    font-size: 48px;
    text-align: center;
    image-rendering: pixelated;
    top: 95%;
    line-height: 20px;
    transform: translateX(-12px);
}

@keyframes wiggle-float-window
{
    from{
        transform:translate(0,0px) rotate(-3deg);
    }
    55%{
        transform:translate(0,8px) rotate(3deg);
    }
    to {
        transform:translate(0,0px) rotate(-3deg);
    }
}